body{
    color: #3b3b3b;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 15px;

}

.carousel-caption{
    top:0;
    position: absolute;
    padding-top: 120px;
    height: 100px;
}
.modal-button{
    background-color: white;
    border-color: #258db0;
    color: #258db0;
    -webkit-transition-duration: 0.3s; /* Safari */
    transition-duration: 0.3s;
    font-size: 14px;
}
.modal-button:hover{
    background-color:#258db0;
    color: white;
    border-color: #258db0;
}
.visit-button{
    background-color:#258db0;
    color: white;
    border-color: #258db0;
}
.visit-button:hover{
    background-color:#207996;
    color: white;
    border-color: #207996;
}
.small-details{
    font-size: 12px;
    margin-top: -5px;
}

.article-title{
    font-weight: bold;
    font-size: 16px;
}

.article-image{
    height: 250px;
    width: 100%;
    position: relative;
    object-fit:cover;

}
.image-caption{
    position: absolute;
  bottom: 8px;
  left: 16px;
  color: white;
}
.nav-heading{
     color: #ededed;
     font-weight: bold;
     float: left;

}

.link-to-article{
  color: #353640;
}
.modal-dialog{
    max-width: 750px;
}
.modal-image{
    max-height: 400px;
    object-fit:cover;
    width: 100%;
    position: relative;
}


.my-nav{
    padding-top: 12px;
    height: auto;
    background-color: #1c1c1c;
    border-bottom: .1px solid #d6d6d6;
    margin:0 auto;
}

.testtest{
    padding:10px;
    position: relative;
}
.nav-bar-toggler{
    background-color: blue;
}
.navbar-large :hover{
    text-decoration: none;

}

.navbar-links:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
    padding-left: 20px;
  left: 0;
  background-color: white;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}
.navbar-links:hover:before {
 visibility: visible;
 -webkit-transform: scaleX(1);
 transform: scaleX(1);

}


.navbar-links{
     color: #ededed;
     position: relative;
     margin-left: 30px;

     font-size: 14px;
     font-weight: bold;
     letter-spacing: 0.15em;
     display: inline;

}

.card{
    margin-top: 15px;
}

.page-heading{
  margin-bottom: 20px;
  margin-top: 35px;
  color: #3b3b3b;
}

@media only screen and (max-width: 500px) {
    .navbar-links{
         color: #ededed;
         margin-left: 0px;
         font-size: 14px;
         font-weight: bold;
         letter-spacing: 0.15em;

    }
    .navbar-large{
        margin-top: 20px;
    }
}
